@use "./mixins/disable-mob-hover" as *;
@use "./mixins/breakpoint" as *;
.video-reviews {
  &__wrap {
    position: relative;
    padding: 0 6rem 2.5rem;
    @include tablet {
      padding: 0 0 2.5rem;
    }
  }
  &__slider {
    overflow: hidden;
  }
}
.video-reviews-item {
  position: relative;
  overflow: hidden;
  display: block;
  height: 596px;
  border-radius: 8px;
  @include tablet {
    height: 636px;
    border-radius: 12px;
  }
  &__name {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    padding: 1.5rem;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 1.375rem;
    line-height: 121%;
    color: #fff;
    @include tablet {
      padding: 1rem;
      font-size: 1.125rem;
    }
  }
  &__image {
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
